
@import "../static/iconfont.scss";
.folder-title[data-v-4ca28e1c] {
  text-align: left;
  color: #fff;
  line-height: 40px;
  padding: 0 10px;
  background-color: #ed5565;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.folder-title.active[data-v-4ca28e1c] {
  background-color: #d60f23;
}
.folder-content[data-v-4ca28e1c] {
  border-top: 1px solid #ed5565;
  transition: all linear .6s;
  /*overflow: hidden;*/
  text-align: left;
  max-height: 1000px;
  overflow: scroll;
  background-color: #fbfdff;
}
.inner-content[data-v-4ca28e1c] {
  padding: 10px;
}
.folder-icon[data-v-4ca28e1c] {
  display: inline-block;
  transition: all ease .6s;
  transform: rotate(0deg);
}
.unfolder[data-v-4ca28e1c] {
  transform: rotate(90deg);
}
.hide[data-v-4ca28e1c] {
  opacity: .3;
  max-height: 0;
}

/* 光环halo */
.halo[data-v-4ca28e1c] {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 9;
}
.halo[data-v-4ca28e1c]:before,
.halo[data-v-4ca28e1c]:after {
  content: '';
  display: block;
  position: absolute;
  width: 240px;
  height: 240px;
  left: -120px;
  top: 0;
  border-radius: 240px;
  animation-name: Grow;
  animation-duration: .3s;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  -webkit-animation-name: Grow;
  -webkit-animation-duration: .3s;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-timing-function: linear;
}
.halo[data-v-4ca28e1c]:before {
  box-shadow: 0 0 100px 10px #fff;
}
.halo[data-v-4ca28e1c]:after {
  box-shadow: inset 0 0 100px 10px #fff;
}
@-webkit-keyframes Grow {
0% {
    transform: scale(0.1);
    -ms-transform: scale(0.1);
    -webkit-transform: scale(0.1);
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    transform: scale(2);
    -ms-transform: scale(2);
    -webkit-transform: scale(2);
    opacity: 0;
}
}




